beforeEach((to, from) => {
  console.log('beforeEachbeforeEach', to, from);
});

router.beforeEach((to, from, next) => {
  console.log('router.beforeEach');
  const title = to.meta && to.meta.title;
  if (title) {
    document.title = title;
  }
  next();
});

export default router;
